export default {
  'url': '/eyesight/peopleanalysis/census',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'census|10': [
        {
          'name|+1': ['九龙坡', '江北', '渝北', '渝中', '南岸', '沙坪坝', '彭水', '璧山', '酉阳', '巴南'],
          'value':'@natural(10,1000)'
        }
      ]
    }
  }
}



