import $ from 'jquery'
import { fetch } from '@/util/request'
import Map from '../../../charts/cqmap'
const map = new Map('.map', {})

export default class Census {
  render() {
    fetch('fetchMap', (data) => {
      map.render(data.map)
    })
  }
}




