import $ from 'jquery'
import listHbs from '../templates/censusList.hbs'
import { fetch } from '@/util/request'

export default class Census {
  render() {
    fetch('fetchCensus', (data) => {
      const max = Math.max(...data.census.map(item => {
        return item.value
      }))
      let renderData = []
      renderData = data.census.map(item => {
        return {
          ...item,
          width: parseFloat(item.value * 100 / max,10)
        }
      })
      $('.census-list').html(listHbs(renderData))
    })
  }
}



