export default {
  'url': '/eyesight/peopleanalysis/age',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'age|6': [
        {
          'name|+1': ['60以前', '60后', '70后', '80后', '90后', '00后'],
          'value': '@natural(10,1000)'
        }
      ]
    }
  }
}



