export default {
  'url': '/eyesight/peopleanalysis/pie',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'carType|5': [
        {
          'name|+1':['涉黄','涉恐', '涉毒','涉疆','涉稳'],
          'value|0-10000': 1,
          'ratio|0-100':1
        }
      ] 
    } 
  }
}  



