import d3 from 'd3'
import getContainer from '../tool/getContainer'
import getSize from '../tool/getSize'
export default class Line {
  // 默认配置项
  defaultSet() {
    return {
      width: 350,
      height: 300,
      margin: {
        top: 20,
        right: 20,
        bottom: 20,
        left: 40
      },
      itemStyle: {
        color: ['#d7b723', '#17f3d1', '#a455f4']
      },
      xAxis: {
        color: '#73bfff',
        fontSize: 18,
        anchor: 'middle',
        mtop: 20 // 顶部偏移量
      },
      yAxis: {
        color: '#fff',
        fontSize: 18,
        mleft: 50 // 往右侧偏移量
      }
    }
  }
  // 初始化，创建容器
  constructor(selector, option) {
    const defaultSet = this.defaultSet()
    this.config = Object.assign(defaultSet, option)
    this.chartName = 'l-line-chart'
    const {
      margin: {
        left
      },
      width,
      height
    } = this.config
    this.size = getSize(selector, width, height)
    const [w, h] = this.size

    this.svg = getContainer(selector, this.chartName, 'svg').attr({
      'width': w,
      'height': h
    })
    this.linePath = d3.svg.line()
      .x((d, i) => {
        return this.xScale(i)
      })
      .y(d => {
        return this.yScale(d.value)
      })
      .interpolate('basis')
  }
  // 绘制
  render(data) {
    const {
      margin: {
        top,
        right,
        bottom,
        left
      }
    } = this.config
    const [width, height] = this.size
    const maxValue = Math.max(...data.map(d => {
      return d.value * 1.2
    }))

    this.xScale = d3.scale.ordinal()
      .domain(d3.range(data.length))
      .rangeRoundBands([20, width - right - left], 0.9)

    this.yScale = d3.scale.linear()
      .domain([0, maxValue])
      .range([0, height - bottom - top])

    this.yAxisScale = d3.scale.linear()
      .domain([0, maxValue])
      .range([height - bottom - top, 0])

    // 绘制折线
    this.drawLine(data)
    // 绘制Y轴
    this.drawYaxis()
    // 绘制X轴
    this.drawXaxis(data)
  }
  // 绘制折线
  drawLine(data) {
    const {
      itemStyle: {
        color
      }
    } = this.config
    const container = getContainer(`.${this.chartName}`, 'line-container', 'g')
    const update = container.selectAll('.l-line').data([data])
    update.enter().append('path').attr('class', 'l-line')
    update.exit().remove()

    container.selectAll('.l-line')
      .classed('active-line', false)
      .attr({
        'd': d => {
          return this.linePath(d)
        },
        'fill': 'none',
        'stroke-width': 3,
        'stroke': (d, i) => {
          return color[i]
        },
        'stroke-opacity': 0.6
      })
      .transition()
      .delay(1000)
      .attr('stroke-opacity', 1)
      .attr('class', 'active-line l-line')
  }
  // 绘制柱图的Y轴
  drawYaxis() {
    const {
      margin: {
        bottom,
        top,
        left
      },
      yAxis: {
        fontSize,
        color,
        mleft
      }
    } = this.config
    const [width, height] = this.size
    const yAxis = d3.svg.axis()
      .scale(this.yAxisScale)
      .orient('left')
      .ticks(5)

    const yAxisDom = getContainer(`.${this.chartName}`, 'y-axis', 'g')
    yAxisDom.attr('transform', `translate(${mleft},${top})`).call(yAxis)
    d3.select(`.${this.chartName}`).select('.y-axis').selectAll('text').style({
      'font-size': fontSize,
      'fill': color
    })
    // 添加一根竖直线
    let yAxisLine = getContainer(`.${this.chartName}`, 'y-axis-line', 'line')
    yAxisLine.attr({
      'x1': left,
      'x2': left,
      'y1': height - bottom,
      'y2': top,
      'stroke': '#637dff',
      'fill': 'none'
    })
  }
  // 绘制X轴
  drawXaxis(data) {
    const {
      margin: {
        right,
        bottom,
        left
      },
      xAxis: {
        color,
        fontSize,
        anchor,
        mtop
      },
      yAxis: {
        mleft
      }
    } = this.config
    const [width, height] = this.size
    const xAxisDom = getContainer(`.${this.chartName}`, 'x-axis', 'g')
    const update = xAxisDom.selectAll('text').data(data)
    update.enter().append('text')
    update.exit().remove()
    xAxisDom.selectAll('text').data(data).attr({
        'x': (d, i) => {
          return this.xScale(i)
        },
        'y': height - bottom + mtop,
        'text-anchor': anchor,
        'font-size': fontSize,
        'fill': color
      })
      .text((d, i) => {
        return d.name
      })
    // 添加一根水平线
    let xAxisLine = getContainer(`.${this.chartName}`, 'x-axis-line', 'line')
    xAxisLine.attr({
      'x1': left,
      'x2': width - left - right,
      'y1': height - bottom,
      'y2': height - bottom,
      'stroke': '#637dff',
      'fill': 'none'
    })
  }
}


