import $ from 'jquery'
import { fetch } from '@/util/request'
import Pie from '../../../charts/pie'
const pie = new Pie('#part-con', {})

export default class Census {
  render() {
    fetch('fetchPie', (data) => {
      pie.render(data.carType)
    })
  }
}


