/**
 * @Author:      zhaojh
 * @DateTime:    2018-01-12 09:11:17
 * @Description: 中重点人员类型分布
 * @Last Modified By:   zhaojh
 * @Last Modified Time:    2018-01-12 13:20:10
 */
import d3 from 'd3'
import _ from 'lodash'
export default class Pie {
  /**
   * 饼图默认配置项
   * @return {object} 默认配置项
   */
  defaultSetting () {
    var width = 340
    var height = 320
    var padding = {
      top: 10,
      right: 0,
      left: 0,
      bottom: 0
    }
    return{
      width, height, padding
    }
  }
  /**
   * 组件配置项
   * @param {string} selector 容器元素选择器
   * @param {object} opt 图表组件配置项
   */
  constructor(selector, opt) {
    let defaultSetting = this.defaultSetting()
    this.config = _.merge({}, defaultSetting, opt)
    
    let { width, height, padding } = this.config  
    let { top, bottom} = padding
    // 创建svg元素
    let svg = d3.select(selector)
      .append('svg')
      .attr('width', width + top)
      .attr('height', height + bottom)
      .style('margin-left', -15)
    this.svg = svg 
      // 创建包裹所有内容的g元素
    this.srcsg = this.svg.append('g')
        .attr('class','srcsg')
        .attr('transform', 'translate(' + (top - 1) + ',' + 10 + ')')
    
  }
  /**
   * 渲染页面  出口文件
   * @param {Object} self 
   */
  render(data) {
    this.pie = d3.layout.pie()
      .value(function(d) {
        return d.value
      })

     // 圆半径
    this.arc1 = d3.svg.arc()
      .innerRadius(50)
      .outerRadius(57)
    this.arc2 = d3.svg.arc()
      .innerRadius(62)
      .outerRadius(64)
    this.drawPie(data)
  }
  
  /**
   * 绘制圆、线、文字 
   * @param {Object} data 文件所需数据 
   */

  drawPie(data) {
    let self = this
    let color = d3.scale.category20()
    let { width, height} = this.config  
    let piedata = this.pie(data)
     // 内圆
    this.srcs1 = this.srcsg.append('g')
      .attr('class', 'pie-1')
      .selectAll('g')
      .data(piedata)
      .enter()
      .append('g')
      .attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')')
    this.srcs1
      .append('path')
      .attr('fill', function(d, i) {
        return color(i)
      })
      .attr('d', function(d) {
        return self.arc1(d)
      })
    
    // 外圆
    this.srcs2 = this.srcsg.append('g')
      .attr('class','pie-2')
      .selectAll('g')
      .data(piedata)
      .enter()
      .append('g')
      .attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')')
    this.srcs2
      .append('path')
      .attr('fill', function(d, i) {
        return color(i)
      })
      .attr('d', function(d) {
        return self.arc2(d)
      })  
    
    // 折线标记绘制
    this.srcs2.append('g')
      .append('polyline')
      .attr('stroke-width', '3')
      .attr('class','line')
      .attr('fill', 'none')
      .attr('stroke', function(d, i) {
        return color(i)
      })
      .attr('points', function(d) {
        return [
          [self.arc2.centroid(d)[0] * 1, self.arc2.centroid(d)[1] * 1],
          [self.arc2.centroid(d)[0] * 1, self.arc2.centroid(d)[1] * 1.6],
          [self.arc2.centroid(d)[0] * 1 < 0 ?
            -90 :
            90, self.arc2.centroid(d)[1] * 1.6
          ]
        ]
      })

    // 文字、占比绘制
    self.srcs2.append('g')
      .attr('calss','name')
      .append('text')
      .attr('transform', function(d) {
        var x = self.arc2.centroid(d)[0] * 1.5 < 0 ?
          -90 :
          90
        var y = self.arc2.centroid(d)[1] * 1.5
        return 'translate(' + x + ',' + y + ')'
      })
      .attr('text-anchor', function(d) {
        return self.arc2.centroid(d)[0] * 1.5 < 0 ?
          'end' :
          'start'
      })
      .attr('fill', '#73bfff')
      .attr('font-size', '18px')
      .text(function(d) {
        return [d.data.name, d.data.ratio + '%'] 
      })

    // 数字绘制
    self.srcs2.append('g')
      .append('text')
      .attr('transform', function(d) {
        var x = self.arc2.centroid(d)[0] * 1.5 < 0 ?
          -110 :
          110
        var y = self.arc2.centroid(d)[1] * 1.5 + 25
        return 'translate(' + x + ',' + y + ')'
      })
      .attr('text-anchor', function(d) {
        return self.arc2.centroid(d)[0] * 1.5 < 0 ?
          'end' :
          'start'
      })
      .attr('fill', '#fff')
      .attr('font-size', '24px')
      .text(function(d) {
        return [d.data.value] 
      })
  } 
}



