import './index.css'
import loader from '@/loader/loader'
import apis from './mock'
import Census from './scripts/census'
import Trend from './scripts/trend'
import Map from './scripts/map'
import Age from './scripts/age'
import Pie from './scripts/pie'

const census = new Census()
const trend = new Trend()
const map = new Map()
const age = new Age()
const pie = new Pie()

loader.load({
  apis: apis,
  init() {
    census.render()
    trend.render()
    map.render()
    age.render()
    pie.render()
  }
}, 1920, 1080)



