import $ from 'jquery'
import { fetch } from '@/util/request'
import Line from '../../../charts/line'
const line = new Line('.trend-chart', {})

export default class Census {
  render() {
    fetch('fetchTrend', (data) => {
      line.render(data.trend)
    })
  }
}


