var echarts = require('echarts')
import { fetch } from '@/util/request'

export default class Census {
  render() {
    const ageRadar = echarts.init(document.getElementById('ageChart'))
    fetch('fetchAge', (data) => {
      const renderData = data.age
      let indicatorText = []
      let randarValue = []
      renderData.map(d => {
        indicatorText.push({
          text:d.name
        })
        randarValue.push(d.value)
      })
      const option = {
        radar: [{
          indicator: indicatorText,
          center: ['50%', '50%'],
          radius: 120,
          startAngle: 90,
          splitNumber: 6,
          shape: 'polygon',
          triggerEvent: true,
          silent: true,
          scale: true,
          nameGap: 2,
          name: {
            formatter: '{value}',
            textStyle: {
              color: '#fff',
              fontSize: 12
            }
          },
          splitArea: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#449cff'
            }
          },
          splitLine: {
            lineStyle: {
              color: '#449cff'
            }
          }
        }],
        series: [{
          name: '重点人员年龄段分布',
          type: 'radar',
          areaStyle: {
            normal: {
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [{
                  offset: 0,
                  color: '#44ff86'
                }, {
                  offset: 1,
                  color: '#0060ff'
                }],
                globalCoord: false
              }
            }
          },
          lineStyle: {
            normal: {
              color: '#00ffff',
              width: 3
            }
          },
          data: [{
            value: randarValue,
            symbol: 'circle',
            symbolSize: 6,
            itemStyle: {
              normal: {
                color: '#01eef0'
              }
            }
          }]
        }]
      }
      ageRadar.setOption(option)
    })
  }
}



