import d3 from 'd3'
import $ from 'jquery'
import * as topojson from 'topojson'
import getContainer from '../tool/getContainer'
import getSize from '../tool/getSize'
import mapJson from './chongqing.json'
import mapBg from '../../apps/peopleAnalysis/images/map-bg.png'
import tooltipTitle from '../../apps/peopleAnalysis/images/tooltipTitle.png'
export default class Map {
  // 默认配置项
  defaultSet() {
    return {
      width: 1120,
      height: 950,
      scaleTimes: 48,
      minR: 20,
      maxR: 30
    }
  }
  // 初始化，创建容器 绘制地图底图
  constructor(selector, option) {
    const defaultSet = this.defaultSet()
    this.config = Object.assign(defaultSet, option)
    this.chartName = 'l-map-chart'
    const {
      width,
      height,
      scaleTimes
    } = this.config
    this.size = getSize(selector, width, height)
    const [w, h] = this.size
    this.svg = getContainer(selector, this.chartName, 'svg').attr({
      'width': w,
      'height': h
    })
    // 放置地图非主城 path
    const area = this.svg.append('g').attr('class', 'area') 
    // 放置地图主城 path
    const mainArea = this.svg.append('g').attr('class', 'main-area') 
    // 放置非主城地区名字 text
    const areaName = this.svg.append('g')
      .attr('class', 'area-name')
      .style('pointer-events', 'none')  
    // 放置非主城地区名字 text
    const mainAreaName = this.svg.append('g')
      .attr('class', 'main-area-name')
      .style('pointer-events', 'none')  
    // 包裹所有的撒点,因为层级问题，所以放在这里
    this.doteWrap = this.svg.append('g')
      .attr('class', 'dote-wrap')
      .style('pointer-events', 'none')  
    this.toolTip = this.svg.append('g')
      .attr('class', 'map-tooltip')
      .style('pointer-events', 'none') // 悬浮框
    const roots = topojson.feature(mapJson, mapJson.objects.chongqing)
    this.features = roots.features
    // 控制地图缩放的大小
    const scale = this.getZoomScale(this.features, w, h)
    const center = this.getCenters(this.features)
    const projection = d3.geo.mercator() // 球形墨卡托投影
      .scale(scale * scaleTimes)
      .center(center)
      .translate([w / 2 - 50, h / 2])
    this.path = d3.geo.path() // 创建一个地理路径生成器
      .projection(projection) // 取得或设置地理投影
    // 添加背景底图
    area.append('image')
      .attr({
        'xlink:href': mapBg,
        'x': -84,
        'y': -230,
        'height': 1120,
        'width': 950,
        'transform': 'scale(1.3,1.5)',
        'webkitTransform': 'scale(1.3,1.5)'
      })
    // 合并主城
    const chong = d3.set(['渝北', '北碚', '江北', '沙坪坝', '南岸', '九龙坡', '大渡口', '巴南', '渝中'])
    let hbc = {
      'name': '主城',
      'path': [],
      'id': '5000'
    }
    hbc.path = topojson.merge(mapJson,
      mapJson.objects.chongqing.geometries.filter(d => {
        return chong.has(d.properties.name)
      })
    )
    this.hbc = hbc
    // 添加地理区域
    area.selectAll('path')
      .data(this.features.filter(d => {
        return !chong.has(d.properties.name)
      }))
      .enter()
      .append('path')
      .attr('d', this.path)
      .attr('stroke-width', 2)
      .attr('stroke', '#54b6ef')
      .attr('data-id', d => {
        return d.properties.id
      })
      .style({
        'fill': '#041c67',
        'opacity': 0.1,
        'cursor': 'pointer'
      })
    mainArea.append('path')
      .datum(hbc.path)
      .attr({
        'd': this.path,
        'fill': '#041c67',
        'stroke': '#54b6ef',
        'opacity': 0.1,
        'cursor': 'pointer',
        'stroke-width': 2
      })
      .attr('data-id', '5000')
    // 添加地区文本名称的背景
    areaName.selectAll('rect')
      .data(this.features)
      .enter()
      .append('rect')
      .attr({
        'x': d => {
          var nameLen = this.gblen(d.properties.name) * 12
          var centroid = this.path.centroid(d)
          return centroid[0] - nameLen / 2
        },
        'y': d => {
          var centroid = this.path.centroid(d)
          return centroid[1] - 15
        },
        'width': d => {
          return this.gblen(d.properties.name) * 12
        },
        'rx': 4,
        'ry': 4,
        'height': 20,
        'fill': d => {
          return chong.has(d.properties.name) ? 'transparent' : '#68c4ff'
        }
      })
    // 添加主城地区文本名称的背景
    mainAreaName.append('rect')
      .datum(hbc.path)
      .attr({
        'x': d => {
          var nameLen = this.gblen('主城') * 12
          var centroid = this.path.centroid(d)
          return centroid[0] - nameLen / 2
        },
        'y': d => {
          var centroid = this.path.centroid(d)
          return centroid[1] - 15
        },
        'width': this.gblen('主城') * 12,
        'rx': 4,
        'ry': 4,
        'height': 20,
        'fill': '#68c4ff'
      })
    // 添加地区名称文本
    areaName.selectAll('text')
      .data(this.features)
      .enter()
      .append('text')
      .attr({
        'text-anchor': 'middle',
        'font-size': 14,
        'fill': '#1c2320',
        'x': d => {
          return this.path.centroid(d)[0]
        },
        'y': d => {
          return this.path.centroid(d)[1]
        }
      })
      .text(d => {
        return chong.has(d.properties.name) ? '' : d.properties.name
      })
    // 添加主城地区名称文本
    mainAreaName.append('text')
      .datum(hbc.path)
      .attr({
        'text-anchor': 'middle',
        'font-size': 14,
        'fill': '#1c2320',
        'x': d => {
          return this.path.centroid(d)[0]
        },
        'y': d => {
          return this.path.centroid(d)[1]
        }
      })
      .text('主城')
  }
  // 绘制
  render(data) {
    const self = this
    const {
      minR,
      maxR
    } = this.config
    const maxValue = d3.max(data, d => {
      return d.value
    })
    const minValue = d3.min(data, d => {
      return d.value
    })
    const rLinear = d3.scale.linear()
      .domain([minValue, maxValue])
      .range([minR, maxR])
    const update = this.doteWrap.selectAll('.dote')
      .data(data)
    var enter = update.enter()
      .append('g')
      .attr('class', 'dote')
      .attr('transform', d => {
        var centroid = null
        this.features.map(item => {
          let typeId = d.id === '5000' ? '500103' : d.id
          if (item.properties.id === typeId) {
            centroid = this.path.centroid(item)
          }
        })
        return `translate(${centroid[0]},${centroid[1]})`
      })
    // .attr('filter', 'url(#GaussianBlur)')
    // update.attr('class', function (d, i) {
    //   return 'domain-g g' + d.id
    // })
    enter.append('circle').attr('class', 'circle1')
    enter.append('circle').attr('class', 'circle2')
    enter.append('circle').attr('class', 'circle3')
    enter.append('circle').attr('class', 'circle4')
    // 四个圆，从里到外，半径依次为  最里面的实心圆R=12
    // 110   100  95   10
    // 100%  90%   85%  
    const wrap = this.doteWrap.selectAll('.dote')
      .data(data)
    wrap.selectAll('.circle1')
      .attr({
        'r': d => {
          return rLinear(d.value)
        },
        'stroke-width': 1,
        'stroke': d => {
          return this.getColor(d.value)
        },
        'fill': 'transparent'
      })
    wrap.selectAll('.circle2')
      .attr({
        'r': d => {
          return rLinear(d.value) * 0.88
        },
        'stroke-width': 2,
        'stroke': d => {
          return this.getColor(d.value)
        },
        'fill': 'transparent'
      })
    wrap.selectAll('.circle3')
      .attr({
        'r': d => {
          return rLinear(d.value) * 0.6
        },
        'stroke-width': 6,
        'stroke': d => {
          return this.getColor(d.value)
        },
        'fill': 'transparent'
      })
    wrap.selectAll('.circle4')
      .attr({
        'r': 6,
        'stroke-width': 2,
        'stroke': 'none',
        'fill': d => {
          return this.getColor(d.value)
        }
      })
    update.exit().remove()
    // 悬浮框 事件
    $('.map').on('mouseenter', 'path[data-id]', function (e) {
      var tooltip = d3.select('.map').select('.map-tooltip')
      var areaId = $(e.target).attr('data-id')
      var centroid = null
      var name
      var value = 0
      if (areaId === '5000') {
        centroid = self.path.centroid(self.hbc.path)
        name = '主城'
      } else {
        self.features.map(item => {
          if (item.properties.id === areaId) {
            centroid = self.path.centroid(item)
            name = item.properties.name
          }
        })
      }
      data.map(function (item) {
        if (item.id === areaId) {
          value = item.value ? item.value : 0
        }
      })
      var config = {
        x1: centroid[0],
        y1: centroid[1],
        x2: centroid[0] + 30,
        y2: centroid[1] - 30,
        delay: 0
      }
      self.drawLine(tooltip, config)
      config.x1 = centroid[0] + 30
      config.y1 = centroid[1] - 30
      config.x2 = centroid[0] + 200
      config.y2 = centroid[1] - 30
      config.delay = 200
      self.drawLine(tooltip, config)
      tooltip.append('rect')
        .transition()
        .ease('linear')
        .delay(200)
        .attr({
          'x': config.x1,
          'y': config.y1,
          'height': 40,
          'width': 170,
          'fill': 'rgba(8,13,51,.8)'
        })
      tooltip.append('image')
        .transition()
        .ease('linear')
        .delay(500)
        .attr({
          'xlink:href': tooltipTitle,
          'x': config.x1 - 10,
          'y': config.y1 - 22,
          'height': 20,
          'width': 150
        })

      tooltip.append('text')
        .transition()
        .ease('linear')
        .duration(800)
        .delay(700)
        .attr({
          'x': config.x1 + 20,
          'y': config.y1 - 6,
          'fill': '#fff',
          'font-size': 18,
          'text-anchor': 'start'
        })
        .text(name)
      tooltip.append('text')
        .transition()
        .ease('linear')
        .duration(800)
        .delay(800)
        .attr({
          'x': config.x1 + 20,
          'y': config.y1 + 28,
          'fill': '#fff',
          'font-size': 18,
          'text-anchor': 'start'
        })
        .text('预警数量 :')
      tooltip.append('text')
        .transition()
        .ease('linear')
        .duration(1000)
        .delay(1000)
        .attr({
          'x': config.x1 + 110,
          'y': config.y1 + 28,
          'font-size': 20,
          'fill': '#44ff86',
          'text-anchor': 'start'
        })
        .text(value)
    })
    $('.map').on('mouseleave', 'path[data-id]', function () {
      self.clearTooltip()
    })
  }
  // 地图缩放
  getZoomScale(features, width, height) {
    var longitudeMin = 100000 // 最小经度
    var latitudeMin = 100000 // 最小维度
    var longitudeMax = 0 // 最大经度
    var latitudeMax = 0 // 最大纬度
    features.forEach((e) => {
      var a = d3.geo.bounds(e); // [[最小经度，最小维度][最大经度，最大纬度]]
      if (a[0][0] < longitudeMin) {
        longitudeMin = a[0][0];
      }
      if (a[0][1] < latitudeMin) {
        latitudeMin = a[0][1];
      }
      if (a[1][0] > longitudeMax) {
        longitudeMax = a[1][0];
      }
      if (a[1][1] > latitudeMax) {
        latitudeMax = a[1][1];
      }
    })
    var a = longitudeMax - longitudeMin;
    var b = latitudeMax - latitudeMin;
    return Math.min(width / a, height / b)
  }
  // 获取中心点
  getCenters(features) {
    var longitudeMin = 100000
    var latitudeMin = 100000
    var longitudeMax = 0
    var latitudeMax = 0
    features.forEach((e) => {
      var a = d3.geo.bounds(e)
      if (a[0][0] < longitudeMin) {
        longitudeMin = a[0][0]
      }
      if (a[0][1] < latitudeMin) {
        latitudeMin = a[0][1]
      }
      if (a[1][0] > longitudeMax) {
        longitudeMax = a[1][0]
      }
      if (a[1][1] > latitudeMax) {
        latitudeMax = a[1][1]
      }
    });
    var a = (longitudeMax + longitudeMin) / 2
    var b = (latitudeMax + latitudeMin) / 2
    return [a, b]
  }
  // 获取字符串宽度
  gblen(str) {
    let len = 0;
    for (let i = 0; i < str.length; i++) {
      if (str.charCodeAt(i) > 127 || str.charCodeAt(i) === 94) {
        len += 2
      } else {
        len++
      }
    }
    return len
  }
  // 根据预警值，获取颜色值
  getColor(value) {
    let color
    if (value > 60) {
      color = '#fe3e3a'
    } else if (value > 30 && value <= 60) {
      color = '#fb8d17'
    } else if (value > 0 && value <= 30) {
      color = '#e4ff00'
    } else if (value <= 0) {
      color = '#44ff86'
    }
    return color
  }
  drawLine(dom, config) {
    dom
      .append('line')
      .attr({
        'x1': config.x1,
        'y1': config.y1,
        'x2': config.x1,
        'y2': config.y1,
        'stroke': '#f4e3bc',
        'stroke-width': 2,
      })
      .transition()
      .ease('linear')
      .duration(200)
      .delay(config.delay)
      .attr({
        'x2': config.x2,
        'y2': config.y2
      })
  }
  clearTooltip() {
    var tooltip = d3.select('.map').select('.map-tooltip')
    tooltip.selectAll('line').remove()
    tooltip.selectAll('image').remove() // 图片背景有兼容性问题
    tooltip.selectAll('rect').remove()
    tooltip.selectAll('text').remove()
  }
}


